<template>
  <div id="main">
    <div class="content page-order-payment">
      <div class="gray-box clear">
        <div class="title">
          <h2></h2>
        </div>
        <div class="box-inner order-info">
          <h3></h3>
          <p class="payment-detail">
            
            <span>24 </span>
          </p>
          <p class="payment-detail"> 72 </p>
        </div>
        <div class="box-inner payment-checkout-panel clear">
          <span class="jianguo-blue-main-btn big-main-btn js-payment-order">
            <a @click="pay"></a>
          </span>
          <span class="prices">
            
            <em>
              <span></span>{{sumitInfo.selectPrice + sumitInfo.freight}}
            </em>
          </span>
        </div>
      </div>
      <div class="confirm-detail">
        <div class="info-title"></div>
        <p class="info-detail">{{sumitInfo.paymentId}}</p>
      </div>
      <div class="confirm-detail">
        <div class="info-title"></div>
        <p class="info-detail">{{sumitInfo.addressInfo.name}}</p>
        <p class="info-detail">{{sumitInfo.addressInfo.phone}}</p>
        <p class="info-detail">{{sumitInfo.addressInfo.add}}</p>
      </div>
      <div class="confirm-detail">
        <div class="info-title"></div>
        <p class="info-detail"></p>
        <p class="info-detail">{{sumitInfo.invoiceHead}}</p>
        <p class="info-detail"></p>
      </div>
      <div class="confirm-table-title clear">
        <span class="name fn-left"></span>
        <span class="subtotal fn-right"></span>
        <span class="num fn-right"></span>
        <span class="price fn-right"></span>
      </div>
      <div class="confirm-goods-table">
        <div class="cart-items clear" v-for="(item, index) in sumitInfo.payItem" :key="index">
          <div class="name fn-left hide-row">
            <div class="name-cell">
              <a
                :title="item.title+''+item.spec_json.show_name+''"
                target="_blank"
              >{{item.title+''+item.spec_json.show_name+''}}</a>
              <br />
            </div>
          </div>
          <div class="subtotal fn-right">
            <div class="subtotal-cell"> {{item.price*item.count}}</div>
          </div>
          <div class="goods-num fn-right">{{item.count}}</div>
          <div class="price fn-right"> {{item.price}}</div>
        </div>
      </div>
      <div class="order-discount-line">
        <p>
          
          <span> {{sumitInfo.selectPrice}}</span>
        </p>
        <p>
          
          <span>+  {{sumitInfo.freight}}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    sumitInfo () {
      return this.$store.state.sumitInfo
    }
  },
  methods: {
    pay () {
      alert('')
      this.$store.commit('isPay')
      this.$router.push({name: 'Order'})
    }
  }
}
</script>

<style>
@import url(../assets/css/payment.css);
</style>



